import { FunctionComponent, useCallback, useEffect } from "react";
import {
    Card,
    CardContent,
    DialogTitle,
    Typography,
    Button,
    Container,
    CircularProgress,
} from "@material-ui/core";
import { useGoogleLogin } from "react-google-login";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";

import { createSession, showNotification } from "../redux/actions";
import { useAppSelector } from "../redux/hooks";
import {
    trackContinueWithGoogle,
    trackContinueWithGoogleFail,
    trackContinueWithGoogleSuccess,
} from "../util/analytics";
import { useQuery } from "../hooks";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "calc(100vh - 64px)",
        backgroundColor: theme.palette.background.default,
    },
    content: {
        padding: 24,
    },
    title: {
        textAlign: "center",
        marginTop: 16,
        padding: 0,
        fontWeight: 500,
        fontSize: 24,
    },
    note: {
        fontSize: 16,
        color: "#606060",
        marginTop: 16,
        maxWidth: 300,
        textAlign: "center",
    },
    logo: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        marginTop: 8,
    },
    logoImage: {
        height: 40,
        width: "auto",
        marginRight: 8,
    },
    logoText: {
        fontFamily: "IBM Plex Sans",
        fontSize: 28,
        fontWeight: 600,
        verticalAlign: "middle",
    },
    container: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    },
    login: {
        marginTop: 24,
        borderRadius: theme.shape.borderRadius,
        width: 256,
        height: 48,
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Params {
    destination: string;
}

const Login: FunctionComponent = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const query = useQuery() as Params;
    const { loading, payload } = useAppSelector((state) => state.session);

    const onSuccess = useCallback(
        (response: any) => {
            trackContinueWithGoogleSuccess();
            let refreshTiming =
                (response.tokenObj.expires_in || 3600 - 5 * 60) * 1000;

            const refreshToken = async () => {
                const newAuthResponse = await response.reloadAuthResponse();
                refreshTiming =
                    (newAuthResponse.expires_in ||
                        newAuthResponse.expires_in ||
                        3600 - 5 * 60) * 1000;

                dispatch(createSession(response.tokenId));
                setTimeout(refreshToken, refreshTiming);
            };

            dispatch(createSession(response.tokenId));
            setTimeout(refreshToken, refreshTiming);
        },
        [dispatch],
    );

    const onFailure = (event: any) => {
        trackContinueWithGoogleFail();
        dispatch(showNotification("Failed to authorize", "error"));
    };

    const { signIn } = useGoogleLogin({
        onSuccess,
        onFailure,
        clientId: process.env.REACT_APP_GOOGLE_OAUTH_CLIENT_ID || "",
        cookiePolicy: "single_host_origin",
        isSignedIn: true,
    });

    useEffect(() => {
        if (payload) {
            history.push(
                query.destination
                    ? decodeURIComponent(query.destination)
                    : "/courses",
            );
        }
    }, [history, query.destination, payload]);

    const handleContinueWithGoogle = useCallback(() => {
        trackContinueWithGoogle();
        signIn();
    }, [signIn]);

    return (
        <Container className={classes.root}>
            <Card>
                <CardContent>
                    <div className={classes.content}>
                        <Typography className={classes.title}>
                            Welcome to AcademyJS
                        </Typography>
                        <div className={classes.container}>
                            <Button
                                disabled={loading}
                                className={classes.login}
                                variant="contained"
                                color="primary"
                                onClick={handleContinueWithGoogle}>
                                {loading && (
                                    <CircularProgress
                                        size="16px"
                                        color="inherit"
                                        className={classes.progress}
                                    />
                                )}
                                Continue with Google
                            </Button>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </Container>
    );
};

export default Login;
